import { motion } from "framer-motion";
function Gap({one,one1, two,two2,  three,three3}){


    return (<>
    <motion.div 
     animate={{
      opacity:[0,1,0]
      }}
      transition={{
        duration: 4,
        ease: "easeInOut",
        times: [0, 0.2, 0.5, 0.8, 1],
        repeat: Infinity,
        repeatDelay: 1
      }}
    className="gap">
        <div
        
        className="gap_con">
            <h1>{one}</h1>
            <p>{one1}</p>
        </div>
        <div 
        
        className="gap_con" style={{marginBottom:50}}>
        <h2>{two}</h2>
        <p>{two2}</p>
        </div>
        <div className="gap_con">
        <h1>{three}</h1>
        <p>{three3}</p>
        </div>
    </motion.div>
    </>);

}

export default Gap;