import { Carousel } from "react-bootstrap";
import Logo from "./Logo";

import { motion } from "framer-motion";
function Home() {
 
  return (
    <>
      <motion.div
     
       initial={{ opacity: 0, scale: 0 }}
       animate={{ opacity: 1, scale: 1 }}
       transition={{
         duration: 2,
        
        //  ease: [0, 0.71, 0.2, 1.01]
         ease: [0, 0, 0, 1]
       }}
      id="home" className="container-fluid ">
        <div className="logo-container class_flex">
          <Logo></Logo>
          <div className="logo-quote">
            <Carousel
              interval={3000}
              indicators={false}
              controls={false}
              pause={false}
            >
              <Carousel.Item>
                <p>
                  "Don't let what you cannot do interfere with what you can do."
                  <br />
                  <br />- Les Brown
                </p>
              </Carousel.Item>
              <Carousel.Item>
                <p>
                  "It's not whether you get knocked down, it's whether you get
                  up."
                  <br />
                  <br />- Vince Lombardi
                </p>
              </Carousel.Item>
              <Carousel.Item>
                <p>
                  "The only way to achieve the impossible is to believe it is
                  possible."
                  <br />
                  <br />- Charles Kingsleigh
                </p>
              </Carousel.Item>
            </Carousel>
          </div>
        </div>
      </motion.div>
    </>
  );
}

export default Home;
