import { Carousel } from "react-bootstrap";
import { motion} from "framer-motion";
import ll from "./images/1.png"
function About(){

    return (<>
     <motion.div
   
     id="about" className="container-fluid">

<div className="hwr-container class_flex"> 
    <div className="hwr-main class_flex">
    <h1>Who We Are</h1>
           </div>
           <div className="hwr-text">
            <div id="carouselExampleSlidesOnly container-fluid " className="carousel slide" data-bs-ride="carousel">
                <div className="carousel-inner">
                <Carousel
              interval={3000}
              indicators={false}
              controls={false}
              pause={false}
            >
              <Carousel.Item>
              <img src={ll} alt="" />
              </Carousel.Item>
              <Carousel.Item>
              <p>Welcome to The Burn! Founded in 2021, 
                            The Burn is more than just a gym
                            it's a community dedicated to health, 
                            fitness, and personal growth. 
                            Since our inception, we have cultivated a
                             supportive and motivating culture, 
                            driven by a team of efficient and highly 
                            skilled trainers committed to helping each 
                            member reach their full potential.</p>
              </Carousel.Item>
              <Carousel.Item>
              <p>In just a few short years, we've grown into a 
                            thriving fitness hub, boasting a membership
                             base of over 500 individuals who share a 
                            passion for wellness and self-improvement. 
                            At The Burn, we believe in the power of
                            community, personalized training, and a 
                            positive environment to ignite your fitness
                            journey.</p>
              </Carousel.Item>
              <Carousel.Item>
              <p>Join us and become a part of The Burn family,
                            where we turn fitness goals into 
                           accomplishments and dreams into reality. 
                           Your journey to a healthier, stronger, and 
                           happier you starts here!</p>
              </Carousel.Item>
              </Carousel>
                                         </div>
        
               
               
              
               
            </div>
            </div>
        </div>
           
         
</motion.div>
    
    </>);

}

export default About;